.habit {
  height: 200px;
  width: 200px;
  background: #fff;
  border-radius: 20px;
  padding: 10px 30px;
  margin: 20px;
  position: relative;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: start;
      justify-content: flex-start;
}

.habit__delete {
  position: absolute;
  top: 15px;
  right: 15px;
  color: #ccc;
}

.delete:hover {
  color: #777;
}

.habit h2 {
  width: 175px;
  height: 55px;
  overflow: hidden;
  -o-text-overflow: ellipsis;
     text-overflow: ellipsis;
}

.habit h2,
.habit h4 {
  font-weight: 400;
}

.habit h4 {
  font-size: 2.5em;
  margin: 0;
}

.tags ul {
  position: absolute;
  bottom: 20px;
  left: 20px;
  padding-left: 0;
  margin: 0;
  width: 170px;
  overflow: scroll;
}

.tags ul li {
  display: inline;
  margin-right: 10px;
  background: #ccc;
  border-radius: 5px;
  padding: 1px 8px;
  font-size: 0.8em;
}

/* ======= PROGRESS BAR ======= */
.progress {
  position: absolute;
  top: 40px;
  right: 30px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: center;
      align-items: center;
}

.progress__bar-outer,
.progress__bar-inner {
  height: 125px;
  width: 20px;
}

/* Bottom */
.progress__bar-outer {
  border-radius: 15px;
  background: -webkit-gradient(linear, left top, left bottom, from(#7b4397), to(#dc2430));
  background: -webkit-linear-gradient(top, #7b4397, #dc2430);
  background: -o-linear-gradient(top, #7b4397, #dc2430);
  background: linear-gradient(to bottom, #7b4397, #dc2430);
  overflow: hidden;
}

/* Top */
.progress__bar-inner {
  background: #ddd;
}

.progress__btn {
  border: 0;
  border-radius: 50%;
  height: 30px;
  width: 30px;
  background: #eee;
  font-size: 0.8em;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  margin-top: 5px;
  outline: none;
}



